import React, { useState } from 'react';
import { AppState } from '../App';
import AccountSummary from './AccountSummary';
import TransactionList from './TransactionList';
import SpendingStats from './SpendingStats';
import Settings from './Settings';

interface DashboardProps {
  state: AppState;
  onLogout: () => void;
  onRefresh: () => void;
}

const Dashboard: React.FC<DashboardProps> = ({ state, onLogout, onRefresh }) => {
  const { userData, accounts, transactions, loading, error } = state;
  const [showSettings, setShowSettings] = useState(false);

  return (
    <div>
      {/* Welcome Header */}
      <div className="row mb-4">
        <div className="col">
          <h2>Welcome, ${userData?.userName}! 👋</h2>
        </div>
        <div className="col-auto">
          <div className="btn-group">
            <button 
              className="btn btn-outline-secondary"
              onClick={onRefresh}
              disabled={loading}
            >
              {loading ? '⏳ Refreshing...' : '🔄 Refresh Data'}
            </button>
            <button 
              className="btn btn-outline-secondary"
              onClick={() => setShowSettings(true)}
            >
              Settings
            </button>
            <button 
              className="btn btn-outline-danger"
              onClick={onLogout}
            >
              Logout
            </button>
          </div>
        </div>
      </div>

      {/* Error Message */}
      {error && (
        <div className="alert alert-danger" role="alert">
          <strong>Error:</strong> {error}
        </div>
      )}

      {/* Loading State */}
      {loading && (
        <div className="text-center py-4">
          <div className="spinner-border text-primary" role="status">
            <span className="visually-hidden">Loading...</span>
          </div>
          <p className="mt-2">Loading your data...</p>
        </div>
      )}

      {/* Main Content */}
      {!loading && (
        <>
          {showSettings ? (
            <Settings onClose={() => setShowSettings(false)} />
          ) : (
            <>
              {/* Financial Health - Full Width */}
              <div className="row g-3 mb-4">
                <div className="col-12">
                  <div className="card">
                    <div className="card-header">
                      <h4 className="mb-0">💰 Financial Health</h4>
                    </div>
                    <div className="card-body">
                      <div className="row text-center">
                        <div className="col-md-3">
                          <div className="p-3">
                            <h3 className="text-success mb-1">${(accounts.reduce((total, account) => total + account.balance, 0)).toFixed(2)}</h3>
                            <p className="text-muted mb-0">Net Worth</p>
                          </div>
                        </div>
                        <div className="col-md-3">
                          <div className="p-3">
                            <h3 className="text-success mb-1">$2,857.51</h3>
                            <p className="text-muted mb-0">Monthly Cash Flow</p>
                          </div>
                        </div>
                        <div className="col-md-3">
                          <div className="p-3">
                            <h3 className="text-success mb-1">89.3%</h3>
                            <p className="text-muted mb-0">Savings Rate</p>
                          </div>
                        </div>
                        <div className="col-md-3">
                          <div className="p-3">
                            <h3 className="text-info mb-1">$1.25</h3>
                            <p className="text-muted mb-0">Round-up Savings</p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              {/* Two-column layout: accounts + stats (left) | transactions (right) */}
              <div className="row g-3" style={{position: 'relative', zIndex: 0}}>
                <div className="col-md-4 col-lg-3 mb-4 position-relative" style={{zIndex: 1}}>
                  <div className="column-clip">
                    <AccountSummary accounts={accounts} />
                    <SpendingStats transactions={transactions} accounts={accounts} />
                  </div>
                </div>
                <div className="col-md-8 col-lg-9 position-relative" style={{zIndex: 1}}>
                  <div className="column-clip">
                    <TransactionList transactions={transactions} />
                  </div>
                </div>
              </div>
            </>
          )}
        </>
      )}
    </div>
  );
};

export default Dashboard;
