import React from 'react';
import { Transaction, Account } from '../App';

interface SpendingStatsProps {
  transactions: Transaction[];
  accounts?: Account[];
}

const SpendingStats: React.FC<SpendingStatsProps> = ({ transactions, accounts = [] }) => {
  // Calculate comprehensive financial metrics
  const calculateStats = () => {
    if (!transactions || transactions.length === 0) {
      return {
        netWorth: 0,
        monthlyCashFlow: 0,
        savingsRate: 0,
        topSpendingCategory: 'N/A',
        largestTransaction: 0,
        spendingTrend: 0,
        averageTransactionSize: 0,
        roundupSavings: 0,
        transactionFrequency: 0,
        accountGrowth: 0
      };
    }

    // Filter out transfers and get current month data
    const nonTransferTxn = transactions.filter(
      transaction => !transaction.description.includes("Transfer")
    );

    const currentDate = new Date();
    const currentMonth = currentDate.getMonth();
    const currentYear = currentDate.getFullYear();

    // Current month transactions
    const currentMonthTxn = nonTransferTxn.filter(txn => {
      const txnDate = new Date(txn.date);
      return txnDate.getMonth() === currentMonth && txnDate.getFullYear() === currentYear;
    });

    // Calculate income and expenses
    const income = currentMonthTxn
      .filter(txn => txn.type === "+")
      .reduce((total, txn) => total + txn.amount, 0);

    const expenses = currentMonthTxn
      .filter(txn => txn.type !== "+")
      .reduce((total, txn) => total + Math.abs(txn.amount), 0);

    // Net worth calculation
    const totalAssets = accounts.reduce((total, account) => total + account.balance, 0);
    const netWorth = totalAssets; // Assuming no liabilities for demo

    // Monthly cash flow
    const monthlyCashFlow = income - expenses;

    // Savings rate
    const savingsRate = income > 0 ? ((income - expenses) / income) * 100 : 0;

    // Top spending category
    const categorySpending: { [key: string]: number } = {};
    currentMonthTxn
      .filter(txn => txn.type !== "+")
      .forEach(txn => {
        const category = txn.tags?.[0] || 'other';
        categorySpending[category] = (categorySpending[category] || 0) + Math.abs(txn.amount);
      });
    
    const topSpendingCategory = Object.keys(categorySpending).reduce((a, b) => 
      categorySpending[a] > categorySpending[b] ? a : b, 'N/A'
    );

    // Largest transaction
    const largestTransaction = Math.max(...nonTransferTxn.map(txn => Math.abs(txn.amount)));

    // Spending trend (compare last 7 days vs previous 7 days)
    const now = new Date();
    const sevenDaysAgo = new Date(now.getTime() - 7 * 24 * 60 * 60 * 1000);
    const fourteenDaysAgo = new Date(now.getTime() - 14 * 24 * 60 * 60 * 1000);

    const lastWeekSpending = nonTransferTxn
      .filter(txn => {
        const txnDate = new Date(txn.date);
        return txnDate >= sevenDaysAgo && txn.type !== "+";
      })
      .reduce((total, txn) => total + Math.abs(txn.amount), 0);

    const previousWeekSpending = nonTransferTxn
      .filter(txn => {
        const txnDate = new Date(txn.date);
        return txnDate >= fourteenDaysAgo && txnDate < sevenDaysAgo && txn.type !== "+";
      })
      .reduce((total, txn) => total + Math.abs(txn.amount), 0);

    const spendingTrend = previousWeekSpending > 0 
      ? ((lastWeekSpending - previousWeekSpending) / previousWeekSpending) * 100 
      : 0;

    // Average transaction size
    const averageTransactionSize = nonTransferTxn.length > 0 
      ? nonTransferTxn.reduce((total, txn) => total + Math.abs(txn.amount), 0) / nonTransferTxn.length 
      : 0;

    // Round-up savings
    const roundupSavings = nonTransferTxn
      .filter(txn => txn.roundup === 'true')
      .reduce((total, txn) => total + (1 - (txn.amount % 1)), 0);

    // Transaction frequency (per day)
    const daysWithTransactions = new Set(nonTransferTxn.map(txn => txn.date)).size;
    const transactionFrequency = daysWithTransactions > 0 ? nonTransferTxn.length / daysWithTransactions : 0;

    // Account growth (simplified - would need historical data for real calculation)
    const accountGrowth = 0; // Placeholder for demo

    return {
      netWorth,
      monthlyCashFlow,
      savingsRate,
      topSpendingCategory,
      largestTransaction,
      spendingTrend,
      averageTransactionSize,
      roundupSavings,
      transactionFrequency,
      accountGrowth
    };
  };

  const stats = calculateStats();

  const formatCurrency = (amount: number) => `$${amount.toFixed(2)}`;
  const formatPercentage = (value: number) => `${value.toFixed(1)}%`;

  return (
    <div className="w-100">
      {/* Spending Insights Card */}
      <div className="card mb-3 w-100">
        <div className="card-header">
          <h4 className="mb-0">📈 Spending Insights</h4>
        </div>
        <div className="card-body">
          <div className="text-center">
            <div className="mb-3">
              <div className="p-2">
                <h5 className="text-primary mb-1">{stats.topSpendingCategory}</h5>
                <p className="text-muted mb-0 small">Top Category</p>
              </div>
            </div>
            <div className="mb-3">
              <div className="p-2">
                <h5 className="text-warning mb-1">{formatCurrency(stats.largestTransaction)}</h5>
                <p className="text-muted mb-0 small">Largest Transaction</p>
              </div>
            </div>
            <div className="mb-3">
              <div className="p-2">
                <h5 className={`mb-1 ${stats.spendingTrend >= 0 ? 'text-danger' : 'text-success'}`}>
                  {stats.spendingTrend >= 0 ? '+' : ''}{formatPercentage(stats.spendingTrend)}
                </h5>
                <p className="text-muted mb-0 small">Spending Trend</p>
              </div>
            </div>
            <div className="mb-3">
              <div className="p-2">
                <h5 className="text-info mb-1">{formatCurrency(stats.averageTransactionSize)}</h5>
                <p className="text-muted mb-0 small">Avg Transaction</p>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Activity Metrics Card */}
      <div className="card mb-3 w-100">
        <div className="card-header">
          <h4 className="mb-0">⚡ Activity Metrics</h4>
        </div>
        <div className="card-body">
          <div className="text-center">
            <div className="mb-3">
              <div className="p-2">
                <h5 className="text-primary mb-1">{stats.transactionFrequency.toFixed(1)}</h5>
                <p className="text-muted mb-0 small">Transactions/Day</p>
              </div>
            </div>
            <div className="mb-3">
              <div className="p-2">
                <h5 className="text-success mb-1">{transactions.length}</h5>
                <p className="text-muted mb-0 small">Total Transactions</p>
              </div>
            </div>
            <div className="mb-3">
              <div className="p-2">
                <h5 className="text-info mb-1">{accounts.length}</h5>
                <p className="text-muted mb-0 small">Active Accounts</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SpendingStats;
